.mini {
    margin: 0 auto;
    padding: 5px;
    width: 580px;
    height: 620px;
    overflow-y: auto;
    background-color: #FAFAFA;
}

.mini label {
    font-weight: initial;
}

.mini .loading-progress {
    position: relative;
    width: 575px;
    height: 600px;
  }

  .mini .loading-progress-bar {
    width: 248px;
    margin-top: 50px;
  }

  .mini .form-horizontal .form-group {
      margin-left: 0;
      margin-right: 0;
  }

  .mini .form-group {
      margin-bottom: 5px;
  }

  .mini .input-sm {
      padding-top: 0;
      padding-bottom: 0;
  }

  .mini .react-bs-table table td, .mini .react-bs-table table th {
      outline: none;
      cursor: pointer;
  }

  .mini .well {
      padding: 3px;
  }

  .mini .radio-inline {
      font-size: smaller;
  }


